body {
    --main: #002654;
}

@media (prefers-color-scheme: light) {
    body {
        --theme-tone-0: #002654;
        --theme-tone-1: #003a80;
        --theme-tone-2: #0056b3;
        --theme-tone-3: #0073e6;
        --theme-tone-4: #0088ff;
        --theme-tone-5: #3399ff;
        --theme-tone-6: #66aaff;
        --theme-tone-7: #99bbff;
        --theme-tone-8: #cce5ff;
        --theme-tone-9: #e6f2ff;
    
        --bw-tone-0: #ffffff;
        --bw-tone-1: #E1E1E1;
        --bw-tone-2: #C2C2C2;
        --bw-tone-3: #A3A3A3;
        --bw-tone-4: #848484;
        --bw-tone-5: #656565;
        --bw-tone-6: #464646;
        --bw-tone-7: #272727;
        --bw-tone-8: #080808;
        --bw-tone-9: #000000;
    }
}

@media (prefers-color-scheme: dark) {
    body {
        --theme-tone-0: #e6f2ff;
        --theme-tone-1: #cce5ff;
        --theme-tone-2: #99bbff;
        --theme-tone-3: #66aaff;
        --theme-tone-4: #3399ff;
        --theme-tone-5: #0088ff;
        --theme-tone-6: #0073e6;
        --theme-tone-7: #0056b3;
        --theme-tone-8: #003a80;
        --theme-tone-9: #002654;

        --bw-tone-0: #000000;
        --bw-tone-1: #080808;
        --bw-tone-2: #272727;
        --bw-tone-3: #464646;
        --bw-tone-4: #656565;
        --bw-tone-5: #848484;
        --bw-tone-6: #A3A3A3;
        --bw-tone-7: #C2C2C2;
        --bw-tone-8: #E1E1E1;
        --bw-tone-9: #ffffff;
    }
}

@media (hover: none) {
    body {
        background-color: var(--bw-tone-1);
        color: var(--bw-tone-7);
    }
}

body {
    padding-bottom: 100px;
}


a {
    text-decoration: none;
}

ul {
    padding: 0;
}